// @flow
import React from 'react';

import './Timer.css';

export function Timer(props) {
  var sec = Math.floor(props.currentTime / 100) < 10 ? "0" + Math.floor(props.currentTime / 100) : Math.floor(props.currentTime / 100);
  var mili = Math.floor(props.currentTime % 100) < 10 ? "0" + Math.floor(props.currentTime % 100) : Math.floor(props.currentTime % 100);
  var showTime = sec + "." + mili;

  return (
    <div className="timerWrapper">
      <div className="timer">Time Left: <span className="timedisplay">{showTime}</span></div>
    </div>
  );
}


