// @flow
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import LatteMilk from '../assets/latte_milk.png';

import {Timer} from './Timer';
import {ImgButton} from './ImgButton';

import './Draw.css';
import milk1 from '../assets/latte-icon1.png';
import milk2 from '../assets/latte-icon2.png';

const milkIcon = [milk1, milk2, null]

const DEFAULT_LINE_WIDTH = 1;
const DEFAULT_LINE_COLOR = 'rgba(255, 255, 255, 0.6)';

export type Coordinates = {
  x: number,
  y: number
}

export default class Draw extends Component {

  loadInterval;
  timer;

  /*
  TODO: Implement drawing in Canvas using HTML5 DeviceMotion events

  This component should only process the acceleration data in the x and y axis only
  to produce suitable drawing coordinates. Computed drawing coordinates should only be within the
  current bounds of the canvas (i.e. current size)

  Canvas instance can be accessed via this.canvas

  */
  constructor(props) {
    super(props);

    this.state = {
      currentStep: 0,//0: not started draw, 1: drawing, 2: finished drawing
      currentTime: 60 * 100,
    }
  
    this.showCupButton = this.showCupButton.bind(this);
  }
    
  static propTypes = {
    drawState: PropTypes.number,
  }

  static defaultProps = {
    lineColor: DEFAULT_LINE_COLOR,
    lineWidth: DEFAULT_LINE_WIDTH
  };

  componentWillMount() {
  }

  componentWillUnmount() {
    this.loadInterval && clearInterval(this.loadInterval);
    this.loadInterval = false;
    this.timer && clearInterval(this.timer);
    this.timer = false;
  }

  componentDidMount() {
    var ctx = this.refs.canvas.getContext('2d');

    if (this.props.drawState === 1){
      var imageObj = new Image();
      imageObj.onload = function() {
        ctx.drawImage(imageObj, 180, 0, 200, 200);
      };
      imageObj.src = LatteMilk;
    }
  }

  startDrawMotion(){

    function Hair(x, y, inkAmount, color) {
      this.x = x || 0;
      this.y = y || 0;
      this.inkAmount = inkAmount;
      this.color = color;

      this._latestPos = { x: this.x, y: this.y };
    }

    Hair.prototype = {
      x:          0,
      y:          0,
      inkAmount:  7,
      color:      '#fff',
      _latestPos: null,

      render: function(ctx, offsetX, offsetY, offsetLength) {
        this._latestPos.x = this.x;
        this._latestPos.y = this.y;
        this.x += offsetX;
        this.y += offsetY;

        var per = offsetLength ? this.inkAmount / offsetLength : 0;
        if      (per > 1) per = 1;
        else if (per < 0) per = 0;

        ctx.save();
        ctx.lineCap = ctx.lineJoin = 'round';
        ctx.strokeStyle = this.color;
        ctx.lineWidth = this.inkAmount * per;
        ctx.beginPath();
        ctx.moveTo(this._latestPos.x, this._latestPos.y);
        ctx.lineTo(this.x, this.y);
        ctx.stroke();
        ctx.restore();
      }
    };


    let ax = 0, ay = 0;
    let vx = 0, vy = 0;
    let x = 500, y = 500;

    let {lineColor, lineWidth} = this.props;

    window.addEventListener("devicemotion", function (event) {
      ax = event.accelerationIncludingGravity.x * 20;
      ay = event.accelerationIncludingGravity.y * 20;
    }, false);

    var ctx = this.refs.canvas.getContext('2d');

    ctx.lineWidth = lineWidth;
    ctx.strokeStyle = lineColor;
    ctx.shadowBlur = 10;
    ctx.shadowColor = 'rgba(255, 255, 255, 0.5)';
    ctx.beginPath();
    ctx.moveTo(x, y);

    var tip = [],
    rad = 35 * 0.5,
    x0, y0, a0, x1, y1, a1, cv, sv,
    i, len;
    var inkAmount = 2, maxHairs = 100, color = 'rgba(255, 255, 255, 0.6)';

    //a1  = Math.PI * 2 * Math.random();
    a1  = 0;
    len = rad * rad * Math.PI / inkAmount | 0;
    if (len < 1) len = 1;
    if (len > this.maxHairs) len = this.maxHairs;

    for (i = 0; i < len; i++) {
      x0 = rad * Math.random();
      y0 = x0 * 0.5;
      a0 = Math.PI * 2 * Math.random();
      x1 = x0 * Math.sin(a0);
      y1 = y0 * Math.cos(a0);
      cv = Math.cos(a1);
      sv = Math.sin(a1);

      tip.push(new Hair(
          x + x1 * cv - y1 * sv,
          y + x1 * sv + y1 * cv,
          inkAmount,
          color
      ));
    }

    var requestAnimationFrame = window.requestAnimationFrame ||
                                window.mozRequestAnimationFrame ||
                                window.webkitRequestAnimationFrame ||
                                window.msRequestAnimationFrame;

    function animate() {

      vy = vy * 0.3 + ay;
      vx = vx * 0.3 - ax;

      vx = vx * 0.98;
      vy = vy * 0.98;

//      ctx.moveTo(x, y);

      var newy, newx;
      newy = parseInt(y + vy / 10);
      newx = parseInt(x + vx / 10);

      if (navigator.userAgent.match(/iPhone|iPad|iPod/i))
      {
        newy = parseInt(y - vy / 10);
        newx = parseInt(x - vx / 10);
      }

      var latestPos = {x: x, y: y};


      if ((newx - 500) * (newx - 500) + (newy - 500) * (newy - 500) < 500 * 500) {
        x = newx;
        y = newy;

        var dx = x - latestPos.x;
        var dy = y - latestPos.y;
        var dist = Math.sqrt(dx * dx + dy * dy);

        for (var j = 0, len = tip.length; j < len; j++) {
          tip[j].render(ctx, dx, dy, dist);
        }
      }
      //if (x-x0)^2 + (y-y0)^2 < r^2


//      ctx.lineTo(x, y);
//      ctx.stroke();
      
      if (self.state.currentStep < 2)
        requestAnimationFrame(animate);
    }
    var self = this;
    animate();
    /*
    this.timer = setInterval( function() {

      vy = vy + ay;
      vx = vx - ax;

      vx = vx * 0.98;
      vy = vy * 0.98;

//      ctx.moveTo(x, y);

      var newy, newx;
      newy = parseInt(y + vy / 20);
      newx = parseInt(x + vx / 20);

      if (navigator.userAgent.match(/iPhone|iPad|iPod/i))
      {
        newy = parseInt(y - vy / 30);
        newx = parseInt(x - vx / 30);
      }

      var latestPos = {x: x, y: y};


      if ((newx - 500) * (newx - 500) + (newy - 500) * (newy - 500) < 500 * 500) {
        x = newx;
        y = newy;

        var dx = x - latestPos.x;
        var dy = y - latestPos.y;
        var dist = Math.sqrt(dx * dx + dy * dy);

        for (var j = 0, len = tip.length; j < len; j++) {
          tip[j].render(ctx, dx, dy, dist);
        }
      }
    }, 10);
    */
  }

  stopDrawMotion(){
    this.timer && clearInterval(this.timer);
    this.timer = false;
  }

  showCupButton(){
    if (this.state.currentStep === 0){
      this.setTimeLimit();
      this.startDrawMotion();
    } else if (this.state.currentStep === 1){
      this.stopDrawMotion();
      this.props.onDrawFinished(this.props.parent);
    }
    this.setState({currentStep: this.state.currentStep + 1})
  }

  setTimeLimit(){
    let self = this;
    this.loadInterval = setInterval(()=>{
      if (self.state.currentTime === 0){
        self.setState({currentStep: 2})
        this.props.onDrawFinished(this.props.parent);
      } else {
        if (self.state.currentStep === 2)
          clearInterval(self.state.loadInterval);
        else
          self.setState({currentTime: self.state.currentTime - 1});
      }
    }, 9);
  }

  render() {
    return (
      <div>
        <ImgButton onClick={()=>this.showCupButton()} imageSrc={milkIcon[this.state.currentStep]} />
        <canvas ref="canvas" id="canvasArea"  width={1000} height={1000}/>
        <Timer currentTime={this.state.currentTime}/>
      </div>
    );
  }
}



