// @flow
import React from 'react';

import './ImgButton.css';

export function ImgButton(props) {
  return (
    <div className="milkIconWrapper" onClick={()=>props.onClick()}>
      {
        props.imageSrc ? <img className="milkIcon" src={props.imageSrc} alt="Milk Icon" /> : null
      }
    </div>
  );
}


