// @flow
import React, { Component } from 'react';
import type { Output } from '../Types';

import {Background} from 'aq-miniapp';

// Import component to be developed as required by specs document here
// import Comp from '../../components/Comp';
import Draw from '../../components/Draw';
import {TapButton} from '../../components/TapButton';

//Import relevant components as required by specs document here

/* Import Assets as required by specs document
ex.
import asset from '../../assets/asset.png';
*/
import bg from '../../assets/latte_art_bg.jpg';


// Import CSS here
import '../css/View2.css';

/* Define constants here

ex.
const MY_CONSTANT = 42;
*/
export type Props = {
  onClick: (Output) => void
};

export class View2 extends Component {
  btnElement = null;

  state: {
    output: Output
  }

  constructor(props) {
    super(props);

    this.onDrawFinished = this.onDrawFinished.bind(this);
  }


  onDrawFinished(self){
    self.btnElement.style.display = "block";
  }

  componentDidMount(){
    this.btnElement.style.display = "none";
  }

  componentWillUnmount () {
  }

  render() {
    return (
      <div className="viewContainer justifyCenter">
        <Background
          image={bg}
        />
          {/* TODO: insert additional assets here as required be the specs document */}
          <div className="coffeeCup">
          </div>
          <Draw onDrawFinished={this.onDrawFinished} parent={this}/>

          <TapButton text="NEXT" onReleased={this.props.onClick} nextbtnRef={el => this.btnElement = el}/>
      </div>
    )
  }
}



